
const nodejs = `
// npm install crypto-js -s 
// npm install axios -s 
import CryptoJS from "crypto-js";
import axios from "axios";
const API_KEY = ""
const API_SECRET = ""
const HOST = "HHOOSSTT"
const api = AAPPII
async function main() {

    let  parameters = ""
    for (const key in api.payload) {
        parameters += key+"="+api.payload[key]+"&"
    }
    if (parameters) {
        parameters = parameters.substring(0, parameters.length - 1)
    }
    await bingXOpenApiTest(api.protocol, HOST, api.uri, api.method, API_KEY, API_SECRET, parameters)
}
main().catch(console.err);
async function bingXOpenApiTest(protocol, host, api, method, API_KEY, API_SECRET, parameters) {
    const timestamp = new Date().getTime()
    if (parameters) {
        parameters = parameters+"&timestamp="+timestamp
    }else {
        parameters = "timestamp="+timestamp
    }
    
    const sign = CryptoJS.enc.Hex.stringify(CryptoJS.HmacSHA256(parameters, API_SECRET))
    const url = protocol+"://"+host+api+"?"+parameters+"&signature="+sign
    console.log("protocol:", protocol)
    console.log("method:", method)
    console.log("host:", host)
    console.log("api:", api)
    console.log("parameters:", parameters)
    console.log("sign:", sign)
    console.log(method, url)
    const  config = {
        method: method,
        url: url,
        headers: {
            'X-BX-APIKEY': API_KEY,
        },
        transformResponse : (resp) => {
            return resp;
        }
    };
   const resp =  await axios(config);
   console.log(resp.status);
   console.log(resp.data);
}`
export default nodejs



